.tags {
  &_list {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin: 0 0 50px -30px;
    list-style-type: none;

    > li {
      margin-left: 30px;
    }
  }
  &_link {
    font-size: 16px;
    color: #181818;
    &:hover {
      text-decoration: none;
    }
  }
}



